import React, { useRef, useEffect } from 'react';
import "./Footer.scss";

const Footer = () => {

  useEffect(() => {
    window.onload=function(){
      var o=document.querySelectorAll(".footer__join");
      //   window.onscroll=function(n){
      //     window.innerHeight+window.scrollY>=document.body.offsetHeight?o[0].classList.add("active"):o[0].classList.remove("active")
      //   }

      var lastScrollTop = 0;
      window.addEventListener("scroll", function(){
        var st = window.pageYOffset || document.documentElement.scrollTop;
        if (st > lastScrollTop){
          o[0].classList.remove("active")
        } else {
          o[0].classList.add("active")
        }
        lastScrollTop = st <= 0 ? 0 : st;
      }, false);
    }
  })


  return (
    <footer className="footer" id="footer">
      <div className="footer__container">
        <div className="footer__container__creators">
          <p className="footer__container__creators__text">
            <span className="footer__container__creators__text__role"></span>
            <span className="footer__container__creators__text__name">Francis</span>
          </p>
          <p className="footer__container__creators__text">
            <span className="footer__container__creators__text__role"></span>
            <span className="footer__container__creators__text__name">Hans</span>
          </p>
          <p className="footer__container__creators__text">
            <span className="footer__container__creators__text__role"></span>
            <span className="footer__container__creators__text__name">Tseng Lee</span>
          </p>
          <p className="footer__container__creators__text">
            <span className="footer__container__creators__text__role"></span>
            <span className="footer__container__creators__text__name">SayidSabrina</span>
          </p>
          <p className="footer__container__creators__text">
            <span className="footer__container__creators__text__role"></span>
            <span className="footer__container__creators__text__name"></span>
          </p>
        </div>
        <div className="footer__container__creators__text">
          <p>
            <span className="footer__container__creators__text__copyright">
              2020 Initium Media. All Rights Reserved.
            </span>
          </p>
        </div>
      </div>
      <div className="footer__join">
        <div className="container">
          <div className="join__text">
            <h3></h3>
            <p></p>
          </div>
          <div className="join__link">
            <a href="https://theinitium.com/subscription/offers/?&amp;utm_source=initium&amp;utm_medium=initium&amp;utm_campaign=initium_project" target="_blank"></a>
          </div>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
